import Vue from "vue";
import Vuex from "vuex";
import axios from "axios";
Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    allProducts: [],
    allUsers: [],
    showProducts: [],
    allImages: [],
    user: -1,
    admin: -1,
    isLogin: false
  },
  getters: {
    getUser: (state) => {
      return state.user
    }
  },
  actions: {
    //*********************PRODUCTS******************************************/
    async fetchProducts({ commit }) {
      const response = await axios.get(
        "http://localhost:8000/api/products" //je rcupre une promesse Javascript = objet avec des infos
      );
      //la requete axios va gnrer un objet avec data
      commit("setProducts", response.data);
    },

    async deleteProducts({ commit }, id) {
      await axios.delete(`http://localhost:8000/api/products/${id}`);
      commit("removeProducts", id);
    },

    async addProducts({ commit }, newproduct) {
      console.log(newproduct);
      newproduct.created_at = new Date().toISOString();
      newproduct.updated_at = new Date().toISOString();

      await axios.post(
        `http://localhost:8000/api/products`,
        newproduct
      ).then(() => {
        console.log(this.state.allProducts)
      })
      commit("addPdt", newproduct);
    },

    async updateProducts({ commit }, data) {
      data.item.updated_at = new Date().toISOString(),
        await axios.put(
          `http://localhost:8000/api/products/${data.item.id}`,
          data.item
        );
      commit("updPdt", data.item);
    },

    async showProducts({ commit }, id) {
      console.log(id),
        await axios.get(`http://localhost:8000/api/products/${id}`);
      commit("showpdt", id);
    },

    //*********************IMAGE******************************************/

    async fetchImages({ commit }) {
      const response = await axios.get(
        "http://localhost:8000/api/images" //je rcupre une promesse Javascript = objet avec des infos
      );
      //la requete axios va gnrer un objet avec data
      commit("setImages", response.data);
    },

    async addImages({ commit }, newimage) {
      const response = await axios.post(
        `http://localhost:8000/api/images`,
        newimage
      );
      console.log(response);
      commit("addImg", newimage);
    },

    async deleteImages({ commit }, id) {
      await axios.delete(`http://localhost:8000/api/images/${id}`);
      commit("removeImages", id);
    },

    //*********************CATEGORIES*****************************************/

    async fetchCategories({ commit }) {
      const response = await axios.get(
        "http://localhost:8000/api/categories" //je rcupre une promesse Javascript = objet avec des infos
      );
      //la requete axios va gnrer un objet avec data
      commit("setCategories", response.data);
    },
    //**********************USERS**************************************/
    async fetchUsers({ commit }) {
      const response = await axios.get(
        "http://localhost:8000/api/users" //je rcupre une promesse Javascript = objet avec des infos
      );
      //la requete axios va gnrer un objet avec data
      commit("setUsers", response.data);
    },

    async deleteUsers({ commit }, id) {
      await axios.delete(`http://localhost:8000/api/users/${id}`);
      commit("removeUsers", id);
    },

    async addUsers({ commit }, newUser) {
      console.log(newUser);
      newUser.created_at = new Date().toISOString();
      newUser.updated_at = new Date().toISOString();

      const response = await axios.post(
        `http://localhost:8000/api/users`,
        newUser
      );
      console.log(response);

      commit("addUser", newUser);
    },

    async updateUsers({ commit }, data) {
      data.item.updated_at = new Date().toISOString(),
        await axios.put(
          `http://localhost:8000/api/users/${data.item.id}`,
          data.item
        );
      commit("updUser", data.item);
    },

    async showUsers({ commit }, id) {
      console.log(id),
        await axios.get(`http://localhost:8000/api/users/${id}`);
      commit("showUser", id);
    },

    //**************************** COOKIES *************************************/
    setCookie(user, data) {
      user = data.user.email
      var admin = data.user.admin
      document.cookie = "admin=" + admin + ";SameSite=Lax;"
      document.cookie = "user_email=" + user + ";SameSite=Lax;"
    },
    getCookie({ commit }, cname) {
      var name = cname + "="
      document.cookie.split(';').forEach(c => {
        if (c.indexOf(name) == 1) {
          if (name == "user_email=") {
            var user = c.substr(name.length + 1, c.length)
            commit('isLogin', true)
            commit('user', user)
          }
          else if (name == "admin=") {
            var admin = c.substr(name.length + 1, c.length);
            commit('admin', admin)
          }
        }
      });
    },
  },

  mutations: {
    //mutations = computed
    setProducts(state, products) {
      //products c'est la data envoye par response.data
      state.allProducts = products;
    },

    removeProducts: (state, id) =>
      (state.allProducts = state.allProducts.filter(
        (product) => product.id !== id
      )), //il va garder l'id diffrent de celui slectionner

    addPdt: (state, newProduct) => state.allProducts.unshift(newProduct),
    //unshit() mthode qui insre les valeurs renseignes au dbut d'un objet ressemblant  un tableau.
    //*********************************USER***********************************************/
    //mutations = computed
    setUsers(state, users) {
      //products c'est la data envoye par response.data
      state.allUsers = users;
    },

    removeUsers: (state, id) =>
      (state.allUsers = state.allUsers.filter(
        (user) => user.id !== id
      )), //il va garder l'id diffrent de celui slectionner

    addUser: (state, newUser) => state.allUsers.unshift(newUser),
    //unshit() mthode qui insre les valeurs renseignes au dbut d'un objet ressemblant  un tableau.
    //*********************************USER***********************************************/

    setCategories(state, categories) {
      state.allCategories = categories;
    },

    updPdt: (state, newProduct) => {
      var id = newProduct.id
      state.allProducts = state.allProducts.filter(
        (product) => product.id !== id
      )
      state.allProducts.unshift(newProduct)
    },
    //unshit() mthode qui insre les valeurs renseignes au dbut d'un objet ressemblant  un tableau.

    showpdt: (state, id) =>
      (state.showProducts = state.allProducts.find(
        (product) => product.id === id
      )),

    //*********************IMAGES*****************************************/

    setImages(state, image) {
      //products c'est la data envoye par response.data
      state.allImages = image;
    },

    addImg: (state, newImage) => state.allImages.unshift(newImage),
    //unshit() mthode qui insre les valeurs renseignes au dbut d'un objet ressemblant  un tableau.


    removeImages: (state, id) =>
      (state.allImages = state.allImages.filter(
        (image) => image.id !== id
      )), //il va garder l'id diffrent de celui slectionner

    /**************************************USER********************************/
    updUser: (state, newUser) => {
      var id = newUser.id
      state.allUsers = state.allUsers.filter(
        (user) => user.id !== id
      )
      state.allUsers.unshift(newUser)
    },
    //unshit() mthode qui insre les valeurs renseignes au dbut d'un objet ressemblant  un tableau.

    showUser: (state, id) =>
      (state.showUser = state.allUsers.find(
        (user) => user.id === id
      )),
    /**************************************USER********************************/


    /************************************** SESSION ****************************/

    isLogin: (state, val) => state.isLogin = val,
    user: (state, user) => state.user = user,
    admin: (state, admin) => state.admin = admin
  },
  modules: {},
});
