<template>
  <v-app>
    <Navbar @logout="logout"/>
    <v-main>
      <router-view></router-view>
    </v-main>
    <Footer />
  </v-app>
</template>

<script>
import Navbar from "./components/Navbar";
import Footer from "./components/Footer";

export default {
  name: "App",

  components: {
    Navbar,
    Footer,
  },

  created() {
    this.$store.dispatch("getCookie", "user_email")
    this.$store.dispatch("getCookie", "admin")
  },

  methods: {
    logout() {
      this.$store.state.user = ''
      this.$store.state.admin = -1
      this.$store.state.isLogin = false
      document.cookie = "user_email= ; expires = Thu, 01 Jan 1970 00:00:00 GMT"
      document.cookie = "admin= ; expires = Thu, 01 Jan 1970 00:00:00 GMT"
      this.$router.push('/')
    }
  }
};
</script>
